.screenshots {
  width: 100vw;
  min-height: 2000px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  box-sizing: border-box;
  padding: 0 100px 0 100px;
  margin-bottom: 100px;
}

.projectimgs {
  background-size: contain;
  background-position: center;
  background-repeat: no-repeat;
  box-sizing: border-box;
  width: 100%;
  height: 100vh;
  margin-top: 100px;
}

.washareimg1 {
  background-image: url("../../assets/graphics/washareimg1.png");
}

.washareimg2 {
  background-image: url("../../assets/graphics/washareimg2.png");
}

.washareimg3 {
  background-image: url("../../assets/graphics/washareimg3.png");
}

.summaryCont {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  width: 100vw;
  min-height: 100vh;
  box-sizing: border-box;
  padding: 100px 280px 100px 280px;
}

.sumDesc {
  font-size: 14pt;
  box-sizing: border-box;
  padding: 0 50px 0 50px;
}

.sumDate {
  font-size: 24pt;
}

.sumTitle {
  font-size: 36pt;
  text-shadow: 3px 3px 10px rgba(25, 25, 25, 0.5);
  font-family: orkney-bold;
  margin: 0;
}

.washareSumImg {
  background-image: url("../../assets/graphics/WashareLogo.svg");
  width: 350px;
  height: 215px;
}

.summaryImg {
  background-size: contain;
  background-repeat: no-repeat;
  box-shadow: 14px 13px 32px -6px rgba(25, 25, 25, 0.43);
}

.sumLinks {
  text-decoration: none;
  color: #191919;
  transition: 0.1s ease-in-out;
  box-sizing: border-box;
  padding: 0 50px 0 50px;
  font-size: 18pt;
}

.sumLinks:hover {
  color: #3bade6;
}

.sumLinksCont {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  width: 100%;
}

@media screen and (max-width: 768px) {
  .summaryCont {
    padding: 100px 10px 100px 10px;
  }

  .summaryImg {
    width: 250px;
    height: 150px;
    background-size: cover;
    background-position: center;
    margin-top: 20px;
  }

  .sumDate {
    font-size: 24pt;
    margin-top: 20px;
  }

  .sumDesc {
    font-size: 12pt;
    box-sizing: border-box;
    padding: 0 10px 0 10px;
    text-align: center;
    margin-top: 20px;
  }

  .sumLinks {
    text-decoration: none;
    color: #3bade6;
    box-sizing: border-box;
    font-size: 14pt;
    padding: 0;
    margin-top: 20px;
  }

  .screenshots {
    width: 100vw;
    min-height: 2000px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    box-sizing: border-box;
    padding: 0;
    margin-bottom: 100px;
  }

  .projectimgs {
    background-size: contain;
    background-position: center;
    background-repeat: no-repeat;
    box-sizing: border-box;
    width: 100%;
    height: 500px;
    margin-top: 0px;
  }
}
